<template>
<el-card>
    <div slot="header">
        <span>邮件展示</span>
    </div>
    <div>
        <el-table :data="aTableData" tooltip-effect="dark" style="width: 100%" max-height="600">
            <el-table-column prop="pid" label="好友角色ID" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="id" label="邮件唯一id" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="title" label="邮件标题" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="content" label="内容" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="type" label="邮件类型" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="time" label="获取时间" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="state" label="状态" min-width="100" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{scope.row.state | statusFilter}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="items" label="邮件奖励" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="tid" label="邮件tid" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column label="操作" min-width="100" fixed="right" v-if="isRole()">
                <template slot-scope="scope">
                    <el-button @click.native.prevent="handleClickDelete(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</el-card>
</template>

<script>
export default {
    name: "achievement-con",
    data() {
        return {
            objQuery: null,
            aTableData: []
        };
    },
    filters: {
        statusFilter(status) {
            const statusMap = ["未查看", "已查看", "已领取"];
            return statusMap[Number(status)];
        }
    },
    watch: {
        $route(to, from) {
            if (to.name == 'role_detail') {
                this.objQuery = this.$route.query;
                this.fetchList();
            }
        },
    },
    methods: {
        isRole() {
            let userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
            let role = userInfo.powerType;
            return role == '超级管理员' || role == '后台客服';
        },
        // 删除
        handleClickDelete(row) {
            this.$confirm("此操作将删除该数据, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                let arr = this.aSel;
                let params = {
                    pid: this.objQuery.pid,
                    serverId: this.objQuery.serverId,
                    id: row.id
                };
                this.$fetch.post(API.role.delete_player_mail, params).then(response => {
                    let res = response.data;
                    if (res.status == 200) {
                        this.fetchList();
                    } else {
                        this.$message.error(res.desc);
                    }
                });
            });
        },
        // 列表
        fetchList() {
            let params = {
                pid: this.objQuery.pid,
                serverId: this.objQuery.serverId
            };
            this.$fetch.post(API.role.select_player_mail, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let data = res.data;
                    this.aTableData = data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.objQuery = this.$route.query;
        this.fetchList();
    }
};
</script>

<style lang="stylus" scoped></style>
