<template>
<el-card>
    <div slot="header">
        <span>成就展示</span>
    </div>
    <div>
        <el-collapse accordion>
            <el-collapse-item v-for="(item,idx) in objRes" :key="idx" :title="item.name" :name="item.type">
                <el-table :data="item.inData" tooltip-effect="dark" style="width: 100%">
                    <el-table-column prop="name" label="成就名" min-width="100" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="time" label="完成时间" min-width="100" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="status" label="状态" min-width="100" show-overflow-tooltip>
                        <template slot-scope="scope">
                            <span>{{scope.row.status | statusFilter}}</span>
                        </template>
                    </el-table-column>
                </el-table>
            </el-collapse-item>
        </el-collapse>
    </div>
</el-card>
</template>

<script>
export default {
    name: "achievement-con",
    data() {
        return {
            objQuery: null,
            objRes: []
        };
    },
    filters: {
        statusFilter(status) {
            const statusMap = ["已领取", "不可领取", "可领取"];
            return statusMap[(Number(status) + 1)];
        }
    },
    watch: {
        $route(to, from) {
            if (to.name == 'role_detail') {
                this.objQuery = this.$route.query;
                this.fetchList();
            }
        },
    },
    methods: {
        // 列表
        fetchList() {
            let params = {
                pid: this.objQuery.pid,
                serverId: this.objQuery.serverId
            };
            this.$fetch
                .post(API.role.select_player_achievement, params)
                .then(response => {
                    let res = response.data;
                    if (res.status == 200) {
                        let data = res.data;
                        this.objRes = data;
                    } else {
                        this.$message.error(res.desc);
                    }
                });
        }
    },
    mounted() {
        this.objQuery = this.$route.query;
        this.fetchList();
    }
};
</script>

<style lang="stylus" scoped></style>
