<template>
<el-card>
    <ul class="base-con">
        <li>
            <span>角色ID</span>
            <span>{{objRes.pid}}</span>
        </li>
        <li>
            <span>服务器ID</span>
            <span>{{objRes.serverId}}</span>
        </li>
        <li>
            <span>禁言时间</span>
            <span>{{objRes.shutUpTime}}</span>
            <el-button-group style="margin-left:10px;" v-if="isRole()">
                <el-button size="small" type="danger" :disabled="false" @click="handleOpenSpeak">禁言</el-button>
                <el-button size="small" type="success" :disabled="false" @click="handleCloseSpeak">解禁</el-button>
            </el-button-group>
        </li>
        <li>
            <span>封停时间</span>
            <span>{{objRes.stopUseTime}}</span>
            <el-button-group style="margin-left:10px;" v-if="isRole()">
                <el-button size="small" type="danger" :disabled="false" @click="handleOpenFree">封停</el-button>
                <el-button size="small" type="success" :disabled="false" @click="handleCloseFree">解封</el-button>
            </el-button-group>
        </li>
        <li v-for="(item,idx) in aForm" :key="idx">
            <span>{{item.tableHead}}</span>
            <span>
                <template v-if="item.updateAble">
                    <el-input v-model="objRes[item.field]" :placeholder="'请输入'+item.tableHead"></el-input>
                </template>
                <template v-else>
                    <p>{{objRes[item.field]}}</p>
                </template>
            </span>
            <!-- <span>{{item.tableHead}}</span>
            <span>{{item.value}}</span> -->
        </li>
    </ul>
    <p class="btn-con">
        <el-button size="small" type="success" @click="handleConfirmUpdate">修改</el-button>
    </p>
    <p class="btn-con" v-if="isRole()">
        <el-button size="small" type="primary" @click="fetchStatus('out')">踢下线</el-button>
        <span>*点击后弹出确认界面，让用户直接与服务器断开连接（避免游戏默认x分钟不与服务器交互，才与服务器断开连接的设置）</span>
    </p>
    <p class="btn-con" v-if="isRole()">
        <el-button size="small" type="danger" @click="fetchStatus('jump')">跳过新手</el-button>
        <span>*点击后弹出确认界面，确定后重登角色即可跳过新手任务（避免用户在新手阶段卡死而无法继续游戏，对于测试也有利）</span>
    </p>

    <el-dialog title="禁言" :visible.sync="bDialogSpeak">
        <el-form :model="objFormSpeak" :rules="rules" ref="objFormSpeak" label-width="150px">
            <el-form-item label="角色Id">
                <p>{{objFormSpeak.pid}}</p>
            </el-form-item>
            <el-form-item label="服务器Id">
                <p>{{objFormSpeak.serverId}}</p>
            </el-form-item>
            <el-form-item label="禁言时间" prop="time">
                <el-date-picker v-model="objFormSpeak.time" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择禁言时间"></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleConfirmSpeak('objFormSpeak')">禁言</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>

    <el-dialog title="封停" :visible.sync="bDialogFree">
        <el-form :model="objFormFree" :rules="rules" ref="objFormFree" label-width="150px">
            <el-form-item label="角色Id">
                <p>{{objFormFree.pid}}</p>
            </el-form-item>
            <el-form-item label="服务器Id">
                <p>{{objFormFree.serverId}}</p>
            </el-form-item>
            <el-form-item label="封停时间" prop="time">
                <el-date-picker v-model="objFormFree.time" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择封停时间"></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleConfirmFree('objFormFree')">封停</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</el-card>
</template>

<script>
export default {
    name: "role-base",
    data() {
        return {
            bDialogSpeak: false,
            bDialogFree: false,
            objFormSpeak: {},
            objFormFree: {},
            objQuery: null,
            aForm: [],
            objRes: {},
            rules: {
                startRewardTime: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }]
            }
        };
    },
    watch: {
        $route(to, from) {
            if (to.name == 'role_detail') {
                this.objQuery = this.$route.query;
                this.fetchList();
            }
        },
    },
    methods: {
        isRole() {
            let userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
            let role = userInfo.powerType;
            return role == '超级管理员' || role == '后台客服';
        },
        // 打开禁言
        handleOpenSpeak() {
            this.objFormSpeak = {
                pid: this.objQuery.pid,
                serverId: this.objQuery.serverId
            };
            this.bDialogSpeak = true;
        },
        // 解开禁言
        handleCloseSpeak() {
            this.$confirm("此操作将解除禁言, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                let params = {
                    pid: this.objQuery.pid,
                    serverId: this.objQuery.serverId
                };
                this.$fetch.post(API.role.free_shut_up, params).then(response => {
                    let res = response.data;
                    if (res.status == 200) {
                        this.fetchList();
                        this.$message.success(res.desc);
                    } else {
                        this.$message.error(res.desc);
                    }
                });
            });
        },
        // 确认禁言
        handleConfirmSpeak(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objFormSpeak));
                    this.$fetch.post(API.role.shut_up, objCopy).then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            this.bDialogSpeak = false;
                            this.fetchList();
                            this.$message.success(res.desc);
                            this.$refs[formName].resetFields();
                        } else {
                            this.$message.error(res.desc);
                        }
                    });
                } else {
                    return false;
                }
            });
        },
        // 打开封停
        handleOpenFree() {
            this.objFormFree = {
                pid: this.objQuery.pid,
                serverId: this.objQuery.serverId
            };
            this.bDialogFree = true;
        },
        // 解除封停
        handleCloseFree() {
            this.$confirm("此操作将解除封停, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                let params = {
                    pid: this.objQuery.pid,
                    serverId: this.objQuery.serverId
                };
                this.$fetch.post(API.role.free_player, params).then(response => {
                    let res = response.data;
                    if (res.status == 200) {
                        this.fetchList();
                        this.$message.success(res.desc);
                    } else {
                        this.$message.error(res.desc);
                    }
                });
            });
        },
        // 确认禁言
        handleConfirmFree(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objFormFree));
                    this.$fetch.post(API.role.player_stop_use, objCopy).then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            this.bDialogFree = false;
                            this.fetchList();
                            this.$message.success(res.desc);
                            this.$refs[formName].resetFields();
                        } else {
                            this.$message.error(res.desc);
                        }
                    });
                } else {
                    return false;
                }
            });
        },
        // 踢下线、跳过
        fetchStatus(type) {
            let url;
            if (type == "out") {
                url = API.role.player_kick_out;
            } else {
                url = API.role.player_jump_guide;
            }
            let params = {
                pid: this.objQuery.pid,
                serverId: this.objQuery.serverId
            };
            this.$fetch.post(url, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let data = res.data;
                    this.fetchList();
                    this.$message.success(res.desc);
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        // 更新
        handleConfirmUpdate() {
            let objCopy = JSON.parse(JSON.stringify(this.objRes));
            this.$fetch
                .post(API.role.update_player, objCopy)
                .then(response => {
                    let res = response.data;
                    if (res.status == 200) {
                        this.bDialog = false;
                        this.fetchList();
                    } else {
                        this.$message.error(res.desc);
                    }
                });
        },
        // 列表
        fetchList() {
            let params = this.objQuery;
            this.$fetch.post(API.role.select_player_one, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let data = res.data;
                    this.objRes = data.player;
                    let headList = data.headList;
                    headList.forEach(item => {
                        item.value = this.objRes[item.field];
                    });
                    let arr = [];
                    headList.forEach(item => {
                        if (
                            item.field !== "pid" &&
                            item.field !== "serverId" &&
                            item.field !== "shutUpTime" &&
                            item.field !== "stopUseTime"
                        ) {
                            arr.push(item)
                        }
                    });
                    this.aForm = arr;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.objQuery = this.$route.query;
        this.fetchList();
    }
};
</script>

<style lang="stylus" scoped>
.base-con {
    width: 700px;

    li {
        list-style: none;
        height: 30px;
        line-height: 30px;
        width: 700px;
        margin-bottom: 10px;

        span {
            float: left;
        }

        span:nth-child(1) {
            width: 100px;
        }
    }
}

.btn-con {
    margin-bottom: 15px;

    span {
        color: red;
        padding: 0 10px;
    }
}
</style>
