<template>
<el-card>
    <div slot="header">
        <span>好友展示</span>
    </div>
    <div>
        <el-form label-width="150px">
            <el-form-item label="好友角色ID">
                <p>{{objData.friendList}}</p>
            </el-form-item>
            <el-form-item label="粉丝角色ID">
                <p>{{objData.fandsList}}</p>
            </el-form-item>
            <el-form-item label="黑名单角色ID">
                <p>{{objData.blackList}}</p>
            </el-form-item>
        </el-form>
    </div>
</el-card>
</template>

<script>
export default {
    name: "friend-con",
    data() {
        return {
            objQuery: null,
            objData: {}
        };
    },
    watch: {
        $route(to, from) {
            if (to.name == 'role_detail') {
                this.objQuery = this.$route.query;
                this.fetchList();
            }
        },
    },
    methods: {
        // 列表
        fetchList() {
            let params = {
                pid: this.objQuery.pid,
                serverId: this.objQuery.serverId
            };
            this.$fetch.post(API.role.select_player_friend, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let data = res.data;
                    this.objData = data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.objQuery = this.$route.query;
        this.fetchList();
    }
};
</script>

<style lang="stylus" scoped></style>
