<template>
<el-card>
    <div slot="header">
        <span>背包展示</span>
    </div>
    <div>
        <el-table :data="aTableData" tooltip-effect="dark" style="width: 100%" max-height="600">
            <el-table-column prop="name" label="道具名称" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="itemId" label="道具id" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="num" label="数量" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="id" label="格子id" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column label="操作" min-width="150" fixed="right" v-if="isRole()">
                <template slot-scope="scope">
                    <el-button @click.native.prevent="handleClickUpdate(scope.row)" type="text" size="small">修改</el-button>
                    <el-button @click.native.prevent="handleClickDelete(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>

    <el-dialog title="修改" :visible.sync="bDialog">
        <el-form :model="objForm" ref="objForm" label-width="150px">
            <el-form-item label="道具id" :rules="[
      { required: true, message: '请填写完整！', trigger: 'blur' }
    ]">
               <!-- <el-input v-model="objForm.itemId" placeholder="请输入道具id"></el-input> -->
                {{objForm.itemId}}
            </el-form-item>
            <el-form-item label="数量" :rules="[
      { required: true, message: '请填写完整！', trigger: 'blur' }
    ]">
                <el-input v-model="objForm.num" placeholder="请输入数量"></el-input>
            </el-form-item>
            <el-form-item label="格子id" :rules="[
      { required: true, message: '请填写完整！', trigger: 'blur' }
    ]">
                <!-- <el-input v-model="objForm.id" placeholder="请输入格子id"></el-input> -->
                {{objForm.id}}
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleConfirmUpdate('objForm')">修改</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</el-card>
</template>

<script>
export default {
    name: "achievement-con",
    data() {
        return {
            objQuery: null,
            objForm: {},
            bDialog: false,
            aTableData: []
        };
    },
    watch: {
        $route(to, from) {
            if (to.name == 'role_detail') {
                this.objQuery = this.$route.query;
                this.fetchList();
            }
        },
    },
    methods: {
        isRole() {
            let userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
            let role = userInfo.powerType;
            return role == '超级管理员' || role == '后台客服';
        },
        // 修改
        handleClickUpdate(row) {
            this.bDialog = true;
            this.objForm = JSON.parse(JSON.stringify(row));
        },
        // 确认修改
        handleConfirmUpdate(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objForm));
                    objCopy.pid = this.objQuery.pid;
                    objCopy.serverId = this.objQuery.serverId;
                    this.$fetch
                        .post(API.role.update_player_item, objCopy)
                        .then(response => {
                            let res = response.data;
                            if (res.status == 200) {
                                this.bDialog = false;
                                this.fetchList();
                            } else {
                                this.$message.error(res.desc);
                            }
                        });
                } else {
                    this.$message.error("保存失败！");
                }
            });
        },
        // 删除
        handleClickDelete(row) {
            this.$confirm("此操作将删除该数据, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                let arr = this.aSel;
                let params = {
                    pid: this.objQuery.pid,
                    serverId: this.objQuery.serverId,
                    id: row.id
                };
                this.$fetch.post(API.role.delete_player_item, params).then(response => {
                    let res = response.data;
                    if (res.status == 200) {
                        this.fetchList();
                    } else {
                        this.$message.error(res.desc);
                    }
                });
            });
        },
        // 列表
        fetchList() {
            let params = {
                pid: this.objQuery.pid,
                serverId: this.objQuery.serverId
            };
            this.$fetch.post(API.role.select_player_bag, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let data = res.data;
                    this.aTableData = data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.objQuery = this.$route.query;
        this.fetchList();
    }
};
</script>

<style lang="stylus" scoped></style>
