<template>
<el-card>
    <div slot="header">
        <span>偃师展示</span>
    </div>
    <div>
        <el-table :data="aTableData" tooltip-effect="dark" style="width: 100%" max-height="600">
            <template v-for="(item,idx) in aTableColumn">
                <el-table-column :key="idx" :prop="item.field" :label="item.tableHead" min-width="120" show-overflow-tooltip></el-table-column>
            </template>
            <el-table-column label="操作" min-width="150" fixed="right" v-if="isRole()">
                <template slot-scope="scope">
                    <el-button @click.native.prevent="handleClickUpdate(scope.row)" type="text" size="small">修改</el-button>
                    <el-button @click.native.prevent="handleClickDelete(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>

    <el-dialog title="修改" :visible.sync="bDialog">
        <el-form :model="objForm" ref="objForm" label-width="150px">
            <el-form-item v-for="(item,idx) in aForm" :key="idx" :label="item.tableHead" :rules="[
      { required: true, message: '请填写完整！', trigger: 'blur' }
    ]">
                <template v-if="item.field=='rid'">
                    <p>{{objForm.rid}}</p>
                </template>
                <template v-else-if="item.field=='pid'">
                    <p>{{objForm.pid}}</p>
                </template>
                <template v-else-if="item.field=='serverId'">
                    <p>{{objForm.serverId}}</p>
                </template>
                <template v-else>
                    <template v-if="item.updateAble">
                        <el-input v-model="objForm[item.field]" :placeholder="'请输入'+item.tableHead"></el-input>
                    </template>
                    <template v-else>
                        <p>{{objForm[item.field]}}</p>
                    </template>
                </template>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleConfirmUpdate('objForm')">修改</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</el-card>
</template>

<script>
export default {
    name: "role-con",
    data() {
        return {
            objQuery: null,
            aForm: [],
            objForm: {},
            bDialog: false,
            aTableData: [],
            aTableColumn: []
        };
    },
    watch: {
        $route(to, from) {
            if (to.name == 'role_detail') {
                this.objQuery = this.$route.query;
                this.fetchList();
            }
        },
    },
    methods: {
        isRole() {
            let userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
            let role = userInfo.powerType;
            return role == '超级管理员' || role == '后台客服';
        },
        // 修改
        handleClickUpdate(row) {
            this.bDialog = true;
            let arr = [];
            this.aForm = JSON.parse(JSON.stringify(this.aTableColumn));
            this.aForm.forEach(item => {
                item.value = row[item.field];
            });
            this.objForm = JSON.parse(JSON.stringify(row));
        },
        // 确认修改
        handleConfirmUpdate(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objForm));
                    this.$fetch
                        .post(API.role.update_role, objCopy)
                        .then(response => {
                            let res = response.data;
                            if (res.status == 200) {
                                this.bDialog = false;
                                this.fetchList();
                            } else {
                                this.$message.error(res.desc);
                            }
                        });
                } else {
                    this.$message.error("保存失败！");
                }
            });
        },
        // 删除
        handleClickDelete(row) {
            this.$confirm("此操作将删除该数据, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                let arr = this.aSel;
                let params = {
                    pid: row.pid,
                    serverId: row.serverId,
                    rid: row.rid
                };
                this.$fetch
                    .post(API.role.delete_role, params)
                    .then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            this.fetchList();
                        } else {
                            this.$message.error(res.desc);
                        }
                    });
            });
        },
        // 列表
        fetchList() {
            let params = {
                pid: this.objQuery.pid,
                serverId: this.objQuery.serverId
            };
            this.$fetch.post(API.role.select_role, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let data = res.data;
                    this.aTableData = data.fieldList;
                    this.aTableColumn = data.headList;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.objQuery = this.$route.query;
        this.fetchList();
    }
};
</script>

<style lang="stylus" scoped></style>
